import { Partner } from './partner';

export class Relation {

    id:number;
	master_partner:Partner;
    slave_partner:Partner;
    status:boolean;
    
    constructor(id,master_partner,slave_partner,status){
        this.id=id;
        this.master_partner=master_partner;
        this.slave_partner=slave_partner;
        this.status=status;
    }
	
	
	
}
