import { Component, OnInit } from '@angular/core';
import { FormGroup, Validators, FormControl } from '@angular/forms';
import { SharedService } from 'app/service/shared-service/shared.service';
import { Partner } from 'app/entities/partner/partner';
import { Company } from 'app/entities/company/company';
import { Router } from '@angular/router';

@Component({
  selector: 'app-add-partner',
  templateUrl: './add-partner.component.html',
  styleUrls: ['./add-partner.component.scss']
})
export class AddPartnerComponent implements OnInit {

  constructor(private sharedservice:SharedService,
              private router:Router) { }

  company:Array<Company>=[];

  
  form_addPartner=new FormGroup({
    partner_name:new FormControl('',[(Validators.required)]), 
    contact_number:new FormControl('',[(Validators.required),(Validators.pattern('[4-9]\\d{9}'))]),
    partner_email:new FormControl('',[(Validators.required)]), 
    branch:new FormControl('',[(Validators.required)]), 
    status:new FormControl('',[(Validators.required)]), 
    password:new FormControl('',[(Validators.required)]),
    company:new FormControl('',[(Validators.required)])
  });

  ngOnInit() {
    this.getCompanyList();
  }

  getCompanyList(){
    this.sharedservice.getCompanyList().subscribe(data => {
      this.company=data;
    },error=> this.sharedservice.sucessMessage("Something went wrong"))
  }

  addPartner(form_addpartner){
    const partner=new Partner(null,this.form_addPartner.get('partner_name').value,
    this.form_addPartner.get('partner_email').value,
    this.form_addPartner.get('password').value,
    this.form_addPartner.get('contact_number').value,
    this.form_addPartner.get('branch').value,
    this.form_addPartner.get('status').value,new Company(this.form_addPartner.get('company').value,null),
    null,null,null,null);
    this.savePartner(partner);
  }

  savePartner(partner:Partner){
    this.sharedservice.addPartner(partner).subscribe(data => {
      if(data>0){
        this.router.navigate(["/admin/partner/partner-list"]);
        this.sharedservice.sucessMessage("Partner Added")
      }
    },error => this.sharedservice.sucessMessage(error.error.message))
  }

}
