import { Component, OnInit } from '@angular/core';
import { SharedService } from 'app/service/shared-service/shared.service';
import { Router } from '@angular/router';
import { Partner } from 'app/entities/partner/partner';
import { FormControl } from '@angular/forms';
import { Condition } from 'app/entities/condition/condition';
import { Pagination } from 'app/entities/condition/pagination';
import { SupportService } from 'app/service/shared-service/support.service';

@Component({
  selector: 'app-partner-list',
  templateUrl: './partner-list.component.html',
  styleUrls: ['./partner-list.component.scss']
})
export class PartnerListComponent implements OnInit {

  constructor(private sharedservice: SharedService,
    private supportservice: SupportService,
    private router: Router) { }


  partner: any;
  index: number = 1;
  text = "";
  length: number = 0;
  pagesize: number = 5;
  pageSizeOptions = [5, 10, 15, 20];
  searchInput: FormControl = new FormControl();

  ngOnInit() {
    this.getPartnerList();
  }

  getPartnerList() {
    const condition = new Pagination(this.index, this.pagesize, this.text);
    this.sharedservice.getPartnerlist(condition).subscribe(data => {
      this.partner = data.data;
      this.length=data.length;
    }, error => this.sharedservice.sucessMessage(error.error.message))
  }

  onPaginateChange(event) {
    this.index = event.pageIndex + 1;
    this.pagesize = event.pageSize;
    this.getPartnerList();
  }

  search(text: string) {
    if (text.length > 1) {
      this.index = 1;
      this.text = text;
      this.getPartnerList();
    }
    if (text.length < 1) {
      this.index = 1;
      this.text = "";
      this.getPartnerList();
    }

  }

  editPartner(partner_id: number) {
    this.supportservice.changeData(partner_id);
    this.router.navigate(["/admin/partner/update-partner"]);
  }



}
