import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { SharedService } from '../../../service/shared-service/shared.service';
import { MasterAdminService } from '../../../service/master-admin-service/master-admin.service';
import { Router } from '@angular/router';
import { ThemeService } from 'ng2-charts';
import { Admin } from 'app/entities/admin/admin';

@Component({
  selector: 'app-master-profile',
  templateUrl: './master-profile.component.html',
  styleUrls: ['./master-profile.component.scss']
})
export class MasterProfileComponent implements OnInit {

  constructor(private sharedservice:SharedService,
              private router:Router) {}
            

profile_pic:File = null ;
profile_status:boolean=false;
admin:Admin;

  ngOnInit() {
    this.getMasterInfo();
  }

  form_updateClientProfile=new FormGroup({
    username:new FormControl({value: '', disabled:true},[(Validators.required)]),
    contact_person:new FormControl('',[(Validators.required)]), 
    contact_number:new FormControl('',[(Validators.required),Validators.pattern('[6-9]\\d{9}')]),
  });

  getMasterInfo(){
    this.sharedservice.getUserInfo(this.sharedservice.getUserEmail()).subscribe(data => {
      this.admin=data.admin;
      this.setFormData(this.admin);
    },error => this.sharedservice.sucessMessage("Something went Wrong"))
  }

  setFormData(admin:Admin){
    this.form_updateClientProfile.setValue({
      username:admin.username,
      contact_person:admin.admin_name, 
      contact_number:admin.contact_number
    })
  }

  selectFile(event){
    this.profile_pic = event.target.files[0] ;
    if(this.profile_pic != null){
      this.admin.profile_pic= Math.floor(100000 + Math.random() * 900000)+"_"+
      this.profile_pic.name;
      this.profile_status=true;
    }
  }

  updateProfile(form_updateclientprofile){
    const admin=new Admin(this.admin.admin_id,this.admin.username,
      this.form_updateClientProfile.get('contact_person').value,null,this.admin.location,
      this.form_updateClientProfile.get('contact_number').value,this.admin.profile_pic);
      this.sharedservice.updateAdmin(admin).subscribe(data => {
        if(data){
          if(this.profile_status){
            this.saveProfilePic();
          }
          else {
            this.sharedservice.sucessMessage("Profile Updated");
          }          
        }
      },error=> this.sharedservice.sucessMessage(error.error.message));
  }

  saveProfilePic(){
    let fd = new FormData();
    fd.append('pic' , this.profile_pic ) ;
    fd.append('file_name' , this.admin.profile_pic);
    this.sharedservice.saveProfilePic(fd).subscribe(data => {
      if(data){        
        window.location.reload(); 
        this.sharedservice.sucessMessage("Profile Updated");    
      }
    },error=> this.sharedservice.sucessMessage(error.error.message))
  }

}
