import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { MasterAdminDashboardComponent } from './master-admin-dashboard/master-admin-dashboard.component';
import { RouterModule } from '@angular/router';
import { MatButtonModule } from '@angular/material/button';
import { MatButtonToggleModule } from '@angular/material/button-toggle';
import { MatIconModule } from '@angular/material/icon';
import { MatListModule } from '@angular/material/list';
import { MatMenuModule } from '@angular/material/menu';
import { MatSelectModule } from '@angular/material/select';
import { MatSlideToggleModule } from '@angular/material/slide-toggle';
import { MatTabsModule } from '@angular/material/tabs';

import { FuseSharedModule } from '@fuse/shared.module';
import { FuseDemoModule } from '@fuse/components/demo/demo.module';
import { FuseHighlightModule, FuseWidgetModule } from '@fuse/components';
import { NgxChartsModule } from '@swimlane/ngx-charts';
import { MatInputModule } from '@angular/material/input';
import { DataTablesModule } from 'angular-datatables';
import { MasterGuardService } from 'app/service/guard/master-guard.service';
import { MasterProfileComponent } from './master-profile/master-profile.component';
import { AddPartnerComponent } from './add-partner/add-partner.component';
import { UpdatePartnerComponent } from './update-partner/update-partner.component';
import { PartnerListComponent } from './partner-list/partner-list.component';
import { MatPaginatorModule } from '@angular/material/paginator';
import { MatDatepickerModule } from '@angular/material/datepicker';
import { MasterReportComponent } from './master-report/master-report.component';
import { ViewReportComponent } from './view-report/view-report.component';
import { ViewCandidatesComponent } from './view-candidates/view-candidates.component';
import { MatDialogModule } from '@angular/material/dialog';


const routes = [
  {
    path: 'dashboard',
    component: MasterAdminDashboardComponent,canActivate:[MasterGuardService]
  },
  {
    path: 'profile',
    component: MasterProfileComponent,canActivate:[MasterGuardService]
  },
  {
    path: 'report',
    component: MasterReportComponent,canActivate:[MasterGuardService]
  },
  {
    path: 'partner',
    children: [
      { path: '', redirectTo: 'partner-list', pathMatch: 'full' },
      { path: 'add-partner', component: AddPartnerComponent,canActivate:[MasterGuardService] },
      { path: 'partner-list', component: PartnerListComponent,canActivate:[MasterGuardService] },
      { path: 'update-partner', component: UpdatePartnerComponent,canActivate:[MasterGuardService] }
    ]
  },

];


@NgModule({
  declarations: [MasterAdminDashboardComponent, MasterProfileComponent, 
    AddPartnerComponent, UpdatePartnerComponent, PartnerListComponent, MasterReportComponent, ViewReportComponent, ViewCandidatesComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    MatButtonModule,
    MatButtonToggleModule,
    MatIconModule,
    MatListModule,
    MatMenuModule,
    MatSelectModule,
    MatSlideToggleModule,
    MatTabsModule,
    MatInputModule,
    FuseWidgetModule,
    NgxChartsModule,
    FuseSharedModule,
    FuseDemoModule,
    FuseHighlightModule,
    DataTablesModule,
    MatDatepickerModule,
    MatPaginatorModule,
    MatDialogModule
  ],
  entryComponents: [
    ViewCandidatesComponent,
    ViewReportComponent
  ],
})
export class MasterAdminModule { }
