export class Admin {

    admin_id:number;
	username:string;
    admin_name:string;
    password:string;
    location:string;
    contact_number:string;
    profile_pic:string;
    
    constructor(admin_id,username,admin_name,password,location,contact_number,profile_pic){
        this.admin_id=admin_id;
        this.username=username;
        this.admin_name=admin_name;
        this.password=password;
        this.location=location;
        this.contact_number=contact_number;
        this.profile_pic=profile_pic;
    }
	
}
