import { Component, OnInit } from '@angular/core';
import { Validators, FormGroup, FormControl } from '@angular/forms';
import { SharedService } from 'app/service/shared-service/shared.service';
import { Router } from '@angular/router';
import { SupportService } from 'app/service/shared-service/support.service';
import { Partner } from 'app/entities/partner/partner';
import { Company } from 'app/entities/company/company';
import { Relation } from 'app/entities/partner/relation';

@Component({
  selector: 'app-update-partner',
  templateUrl: './update-partner.component.html',
  styleUrls: ['./update-partner.component.scss']
})
export class UpdatePartnerComponent implements OnInit {

  constructor(private sharedservice:SharedService,
              private router:Router,
              private supportservice:SupportService) { }

  partner_id:number;
  partner:Partner;
  partnerList:any;
  company:Array<Company>=[];
  partnerDropDown:any;

  form_addPartner=new FormGroup({
    partner_name:new FormControl('',[(Validators.required)]), 
    contact_number:new FormControl('',[(Validators.required),(Validators.pattern('[4-9]\\d{9}'))]),
    partner_email:new FormControl({disabled:true,value:''},[(Validators.required)]), 
    branch:new FormControl('',[(Validators.required)]), 
    status:new FormControl('',[(Validators.required)]), 
    company:new FormControl('',[(Validators.required)]),
    relation:new FormControl()
  });

  getCompanyList(){
    this.sharedservice.getCompanyList().subscribe(data => {
      this.company=data;
    },error=> this.sharedservice.sucessMessage("Something went wrong"))
  }

  ngOnInit() {
    this.supportservice.currentdata.subscribe(data => this.partner_id=data);
    this.checkForData(this.partner_id);
  }

  checkForData(partner_id:number){
    if(partner_id!=0){
      this.getCompanyList();
      this.getPartnerDropDown();
      this.getPartnerInfo(partner_id);
    }
    else{
      this.router.navigate(["/admin/partner/partner-list"]);
    }    
  }

  getPartnerInfo(partner_id:number){
    this.sharedservice.getPartnerById(partner_id).subscribe(data => {
      this.partner=data;
      this.form_addPartner.setValue({
        partner_name:this.partner.partner_name, 
        contact_number:this.partner.contact_number,
        partner_email:this.partner.partner_email, 
        branch:this.partner.branch, 
        status:this.partner.status, 
        company:this.partner.company.company_id,
        relation:this.partner.relation.filter(x => x.status==true)
                                      .map(x => x.slave_partner.partner_id)
      })
    },error => this.sharedservice.sucessMessage(error.error.message))
  }

  getPartnerDropDown(){
    this.sharedservice.getPartnerForName(this.partner_id).subscribe(data => {
      this.partnerDropDown=data;
    },error => this.sharedservice.sucessMessage(error.error.message))
  }

  updatePartner(form_addpartner){
    this.partner.partner_name=this.form_addPartner.get('partner_name').value;
    this.partner.status=this.form_addPartner.get('status').value;
    this.partner.contact_number=this.form_addPartner.get('contact_number').value;
    this.partner.company.company_id=this.form_addPartner.get('company').value;
    this.partner.branch=this.form_addPartner.get('branch').value;
    let partner:number[]=this.form_addPartner.get('relation').value;
    if(partner.length>0){
      this.partner.relation.forEach(element => {
          if(!partner.includes(element.slave_partner.partner_id)){ 
            element.status=false;
          }
          else{
            element.status=true;
          }
      });
      partner.forEach(element => {        
        if(!this.partner.relation.map(x => x.slave_partner.partner_id).includes(element)){
          console.log("here2");          
          const relation=new Relation(null,new Partner(this.partner_id,null,null,null,
            null,null,null,null,null,null,null,null),
            new Partner(element,null,null,null,null,null,null,null,null,null,null,null),true);
          this.partner.relation.push(relation);
        }
      });  
    }
    if(partner.length<1 && this.partner.relation.length>0){
      this.partner.relation.forEach(element => {
        element.status=false;
      });
    }
    this.sharedservice.updatePartner(this.partner).subscribe(data => {
      if(data){
        this.sharedservice.sucessMessage("Partner Updated")
        this.router.navigate(["/admin/partner/partner-list"]);
      }
    },error => this.sharedservice.sucessMessage(error.error.message))
  }

}
